.container {
  padding-top: 30px;
}
.form {
  margin-left: auto;
  margin-right: auto;
  padding: 40px 30px;
  border-radius: 10px;
  border: 1px solid gray;
  box-shadow: 0px 25px 30px 3px rgba(0, 0, 0, 0.3);
  background-color: white;
  overflow: hidden;
  font-weight: 800;
  width: 500px;
  font-size: 18px;
}
.formDark {
  margin-left: auto;
  margin-right: auto;
  padding: 40px 30px;
  border-radius: 10px;
  border: 1px solid gray;
  box-shadow: 0px 25px 30px 3px rgba(0, 0, 0, 0.3);
  background-color: rgba(255, 255, 255, 0.3);
  color: white;
  overflow: hidden;
  font-weight: 800;
  width: 500px;
  font-size: 18px;
}
.label{
  display: flex;
  justify-content: space-between;
  align-items: center;
   
}
.label:not(:last-child){
   margin-bottom: 10px;
}
.input {
  margin-left: 10px;
  width: 300px;
}
.btn {
  margin-top: 25px;
  padding: 10px 30px;
  border-radius: 10px;
  overflow: hidden;
  font-weight: 800;
  text-transform: capitalize;
  background-color: transparent;
  border: 1px solid #16014c;
  box-shadow: 0px 25px 30px 3px rgba(0, 0, 0, 0.3),
    0 0 25px 0 rgba(22, 1, 76, 0.8) inset, 0 0 30px 4px rgba(230, 209, 108, 0.8);
}
.btn:hover,
.btn:focus {
  outline: none;
  border-color: #e6d16c;
  box-shadow: 0px 25px 30px 3px rgba(0, 0, 0, 0.3),
    0 0 25px 0 rgba(230, 209, 108, 0.8) inset, 0 0 30px 4px rgba(22, 1, 76, 0.8);
}
.btnDark {
  margin-top: 25px;
  padding: 10px 30px;
  border-radius: 10px;
  overflow: hidden;
  font-weight: 800;
  text-transform: capitalize;
  background-color: transparent;
  border: 1px solid #e6d16c;
  box-shadow: 0px 25px 30px 3px rgba(0, 0, 0, 0.3),
    0 0 25px 0 rgba(230, 209, 108, 0.8) inset, 0 0 30px 4px rgba(22, 1, 76, 0.8);
}
.btnDark:hover,
.btnDark:focus {
  outline: none;
  border-color: #16014c;
  box-shadow: 0px 25px 30px 3px rgba(0, 0, 0, 0.3),
    0 0 25px 0 rgba(22, 1, 76, 0.8) inset, 0 0 30px 4px rgba(230, 209, 108, 0.8);
}
